@import '../config';
@import '../core/breakpoints';

.mImages {
	margin-bottom: 20rem;
	@include max940px {
		margin-bottom: 10rem;
	}

	@include max480px {
		margin-bottom: 7.5rem;
	}

	.mImages__row {
		justify-content: flex-start;
		@include max940px {
			flex-direction: column;
		}
	}

	.mImages__row--image-first {
		@include max940px {
			align-items: center;
		}

		.mImages__col--copy {
			margin-right: 0;
			margin-left: 0;
		}

		.mImages__col--img {
			order: -1;
			margin-right: 305px;
			@media screen and (max-width: 1250px) {
				margin-right: 195px;
			}

			@media screen and (max-width: 1150px) {
				margin-right: 155px;
			}

			@include max940px {
				order: 0;
				margin-right: 0;
			}
		}
	}

	.mImages__col--copy {
		max-width: 475px;
		width: 100%;
		padding-top: 5rem;
		margin-right: 210px;
		@media screen and (max-width: 1250px) {
			margin-right: 100px;
		}

		@media screen and (max-width: 1150px) {
			margin-right: 60px;
		}

		@include max940px {
			max-width: 540px;
			margin: 0 auto;
			padding-top: 0;
		}
	}

	.mImages__col--img {
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		gap: 20px;
		max-width: 420px;
		width: 100%;
		@include max940px {
			max-width: 100%;
		}
	}

	.mImages__imageInner {
		position: relative;
		display: flex;
		flex-direction: column;
		gap: 20px;
		width: 100%;
		@include max940px {
			align-items: center;
		}
	}

	.mImages__symbol {
		display: block;
		max-width: 475px;
		width: 100%;
		@media screen and (max-width: 1150px) {
			max-width: 400px;
		}

		@include max940px {
			max-width: 485px;
			margin-left: -100px;
		}
	}

	.mImages__image {
		position: absolute;
		display: block;
		top: 10px;
		right: -95px;
		max-width: 360px;
		width: 100%;
		@media screen and (max-width: 1150px) {
			max-width: 320px;
			right: -60px;
		}

		@include max940px {
			top: 0;
			left: 50%;
			transform: translateX(-50%) !important;
			right: auto;
			margin-left: 100px;
		}

		img {
			display: block;
			width: 100%;
			height: 100%;
		}

		@include max940px {
			max-width: 400px;
		}
	}

	.mImages__subheadline {
		display: block;
		font-size: 18px;
		line-height: 110%;
		letter-spacing: 0.45em;
		text-transform: uppercase;
		color: #32343E;
		margin-bottom: 40px;
		font-family: $circular;
		font-weight: 900;
		@include max940px {
			text-align: center;
		}

		@include max480px {
			margin-bottom: 20px;
			font-size: 16px;
		}
	}

	.mImages__headline {
		font-weight: 900;
		font-size: 41px;
		line-height: 1.3;
		color: $white;
		margin-bottom: 40px;
		@include max940px {
			text-align: center;
		}

		@include max480px {
			font-size: 30px;
		}
	}

	.mImages__text,
	.mImages__text p {
		font-size: 20px;
		line-height: 1.6;
		color: #8F93A8;
		font-weight: 400;
		@include max940px {
			text-align: center;
		}

		@include max480px {
			font-size: 18px;
		}
	}

	.mImages__text {
		@include max940px {
			margin-bottom: 5rem;
		}
	}
}